.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



* {
  font-family:  Helvetica, sans-serif !important;
}

@media (min-width: 500px) {
  main {
    width: 500px;
    margin: 0 auto;
  }
  .full-width {
    margin-left: 0;
    margin-right: 0;
  }
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}

.embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


html, body, h1, footer {
  font-family:  Helvetica, sans-serif !important; /*Specify your font name here*/
  -ms-text-size-adjust: 100% !important;
  -webkit-text-size-adjust: 100% !important;
  
}


:root {
  --firstColor:#024059;
  --secondColor: #C14328;
  --thirdColor: #46C6E9;
  --fourthColor: white;
  
  --fontColor: black;
  --crustaColor: #F3634E;
}

/* futura */ 
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url(/static/media/futur.71d20cae.ttf);
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url("/static/media/Futura Bold font.7bfa5614.ttf");
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url("/static/media/Futura Light Italic font.7a38f125.ttf");
  font-style: italic;
  font-weight: normal;
}
