
* {
  font-family:  Helvetica, sans-serif !important;
}

@media (min-width: 500px) {
  main {
    width: 500px;
    margin: 0 auto;
  }
  .full-width {
    margin-left: 0;
    margin-right: 0;
  }
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}

.embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


html, body, h1, footer {
  font-family:  Helvetica, sans-serif !important; /*Specify your font name here*/
  -ms-text-size-adjust: 100% !important;
  -webkit-text-size-adjust: 100% !important;
  
}


:root {
  --firstColor:#024059;
  --secondColor: #C14328;
  --thirdColor: #46C6E9;
  --fourthColor: white;
  
  --fontColor: black;
  --crustaColor: #F3634E;
}

/* futura */ 
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url('../src/fonts/futura/futur.ttf');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url('../src/fonts/futura/Futura\ Bold\ font.ttf');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'futura';
  src: local('futura'),
       url('../src/fonts/futura/Futura\ Light\ Italic\ font.ttf');
  font-style: italic;
  font-weight: normal;
}
/* bodoni */ 
@font-face {
  font-family: 'bodoni';
  src: local('bodoni'),
       url('../src/fonts/bodoni/bodoni.ttf');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'bodoni';
  src: local('bodoni'),
       url('../src/fonts/bodoni/Bodoni\ Bold.ttf');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'bodoni';
  src: local('bodoni'),
       url('../src/fonts/bodoni/Bodoni\ Italic.ttf');
  font-style: italic;
  font-weight: normal;
}


.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.wrapper {
  margin-right: 0; /* 1 */
  margin-left:  0; /* 1 */

  min-width: 100%; /* 2 */
  min-height: 100%; /* 2 */

  padding-right: 0; /* 3 */
  padding-left:  0; /* 3 */
}

footer {
  background-color: var(--firstColor);
  color: var(--fourthColor);
  margin-top: auto;
  
}

.routerHeight {
  min-height: 68.5%; /* 2 */
}
html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 0;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}



Main {
  background-color: var(--fourthColor);
  color: var(--fontColor);
}

header {
  background-color: var(--fourthColor);
}

.routesContainer {
  min-height: 100%;
}
.containerMargins {
  margin-left: 10%;
  margin-right: 10%;

}
.sectionHeader {
  color: var(--secondColor);
  font-size: 200%  !important;
  font-weight: bold !important;
}

.textColorHeader {
  color: var(--secondColor);
  font-size: 150% !important;
  font-weight: bold !important;
}

.textHeader {
  color: var(--fontColor);
  font-weight: bold !important;
  font-size: 120% !important;
  text-align: left;
}

.labText {
  color: var(--fontColor);
  font-size: 100% !important;
  text-align: justify;
}

/* Main View */ 

.square1 {
  background-color: var(--firstColor);
  color: white;
}

.square2 {
  background-color: var(--secondColor);
  color: white;
}

.square3 {
  background-color: var(--thirdColor);
  color: white;
}

.square4 {
  background-color: #F3634E;
  color: white;
}


/* Post View */ 

.postContainer {
  background-color: red;
  min-height: 100%;
  height: 100%;
}

.img-Max-Post {
  max-width: 300px;
}

.clickable {
  cursor: pointer;
}

.secondColorText {
color: var(--firstColor) !important ;
}



iframe {
  width: 100%;
  height: 100%;
}
